
import { defineComponent, computed } from 'vue'
import { oneOf } from 'vue-types'
export default defineComponent({
  name: 'SvgIcon',
  props: {
    prefix: {
      type: String,
      default: 'icon',
    },
    name: {
      type: String,
      required: true,
    },
    spin: {
      type: Boolean,
      default: false,
    },
    // 图标类型，目前可用类型： line 线性
    type: oneOf(['', 'line']).def(''),
  },
  setup(props) {
    const symbolId = computed(() => `#${props.prefix}-${props.name}`)

    return { symbolId, prefixCls: 'svg-icon' }
  },
})
